@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
	.navlink {
		@apply text-lg italic font-mono font-bold text-center mx-2 my-1 px-2 py-2 select-none text-black bg-sky-500;
	}
	.active {
		@apply underline  decoration-stone-200 decoration-wavy;
	}

	.input-form {
		@apply font-mono text-xl text-stone-200 border-b-2 focus:outline-none;
	}
	* {
		@apply text-slate-700 bg-black/0;
	}
	:root {
		@apply bg-sky-200;
	}
}

.gridResponsive {
	display: grid;
	grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
	gap: auto;
}
